<template>
	<div style="margin-right: 20px;">
		<div class="content">
			<el-form :model="forms" ref="form" label-width="60px" label-position="left">
				<div>
					<el-form-item label="" prop="time">
						<el-date-picker type="date" v-model="forms.time" format="yyyy  MM  dd " value-format="yyyy-MM-dd" placeholder=""></el-date-picker>
					</el-form-item>
				</div>
				<div>
					<el-form-item label="">
						<el-select v-model="forms.classId" placeholder="">
							<el-option
									v-for="(item,index) in theArticleClassifications"
									:key="index"
									:label="item.classify"
									:value="item._id"
							></el-option>
						</el-select>
					</el-form-item>
				</div>
				<div>
					<el-form-item label="" prop="title">
						<el-input v-model="forms.title" type="text" placeholder="..." size="samll" clearable></el-input>
					</el-form-item>
				</div>
				<div>
					<el-form-item label="" prop="content">
						<el-input v-model="forms.describe" type="textarea" placeholder="..." size="samll" clearable></el-input>
					</el-form-item>
				</div>
				<!---->
				<div>
					<el-form-item label="" prop="url">
						<el-upload class="uplod"
								   action="/api/upload/activityUpload"
								   accept=""
								   :on-success="handleSuccess"
								   :before-upload="beforeUpload">
							<img v-if="forms.paging" :src="forms.paging" alt="" class="uplodImg">
							<i v-else class="el-icon-plus avatar-uploader-icon"></i>
						</el-upload>
					</el-form-item>
				</div>
			</el-form>
			<!---->
			<div id="wangeditor">
				<div ref="editorElem" style="text-align:left;"></div>
			</div>
			<div slot="footer">
				<el-button
						type="primary"
						@click="saveThePaper('form', type)"
						style="margin-left: 486px;"
						v-text="type==='add'?'':''"
				 />
			</div>
		</div>
	</div>
</template>

<script>
    import {ajax} from "../../../api";
    import {addArticle,updateArticle} from "../../../api/adminRouter/article";
    import { Message} from 'element-ui';
    import {mapActions} from 'vuex';
    import E from "wangeditor";
    export default {
        data () {
            return {
                type: 'add',
                editorOption: {},
                forms: {
                    time: '',
                    classId:'',
                    paging: '',
                    title: '',
                    describe: '',
                    content:'',
                },
                theArticleData: [],
                theArticleClassifications:[],
                editor: null,
                editorContent: ''
            }
        },
        methods: {
            ...mapActions(['theArticleClassification']),
            beforeUpload(file) {
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isLt2M) {
                    this.$message.error(' 2MB!');
                }
                return isLt2M;
            },
            handleSuccess(data) {
                this.forms.paging = data.imgName;
                Message({
                    type: data.type,
                    message: data.particulars
                });
            },
            saveThePaper(form) {
                this.$refs[form].validate(async  valid => {
                    if (valid) {
                        //html
                        this.forms.content = this.editor.txt.html();
                        let reslut = null;
                        if(this.type === "add"){
                            reslut = await addArticle(this.forms);
						}else{//
                            reslut= await updateArticle(this.forms);
                        }
                        let {data} = reslut;
                        Message({
                            type: data.type,
                            message: data.particulars
                        });
                        if(!data.code){//
                            this.$router.push({name:"toViewTheArticle"});
                        }
                        
                    }
                })
            },
            eliminate(){
                this.forms =  {
                    time: '',
                    classId:'',
                    paging: '',
                    title: '',
                    describe: '',
                    content:'',
                }
			},
        },
        computed:{
            CheckTheClassificationData(){
                return this.$store.state.theArticleClassification;
            },
        },
        watch:{
            CheckTheClassificationData(){
                let data = this.$store.state.theArticleClassification;
                this.theArticleClassifications =  data;
            }
        },
		activated(){
            this.eliminate();
            this.editor.txt.clear();
            let index = this.$route.params.index;
            this.type = "add";
			if(index===0 ||index){
			    this.type = "update";
			    let  data = this.$store.state.articleData.articleDate[index];
				this.forms = data;
                this.editor.txt.html(data.content);
                this.forms.classId = data.classId._id;
			}
            this.theArticleClassification();
		},
        mounted() {
            this.editor = new E(this.$refs.editorElem);
            this.editor.customConfig.uploadImgMaxSize = 2 * 1024 * 1024 //
            this.editor.customConfig.uploadImgMaxLength = 10 //
            this.editor.customConfig.uploadFileName = 'file'
            this.editor.customConfig.uploadImgParams = {//
                token: 'articleContent'
            }
            this.editor.customConfig.withCredentials = true;  //
            this.editor.customConfig.uploadImgServer = '/api/upload/activityContent';
            this.editor.customConfig.uploadImgHooks = {
                customInsert: function (insertImg, result, editor) {
                    let url = result.imgName
                    insertImg(url)
                }
            }
            this.editor.customConfig.menus = [
                // 
                'head', // 
                'bold', // 
                'fontSize', // 
                'fontName', // 
                'italic', // 
                'underline', // 
                'strikeThrough', // 
                'foreColor', // 
                'backColor', // 
                'link', // 
                'list', // 
                'justify', // 
                'quote', // 
                'emoticon', // 
                'image', // 
                'table', // 
                'code', // 
                'undo', // 
                'redo' // 
            ];
            this.editor.create(); // 
        },
    }
</script>

<style scoped lang="scss">
	.uplod{
		width: 178px;
		height: 178px;
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}
	.uplodImg{
		width: 178px;
		height: 178px;
		display: block;
	}
	.avatar-uploader-icon{
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}
	.editor{
		margin-bottom: 20px;
	}
</style>
